// Core
import React, { useEffect, useState, useCallback } from "react";
import { useObserver } from "mobx-react";
// UI
import FilterSelectorPanel from "./FilterSelectorPanel";
import { Container, Box, Paper } from "@material-ui/core";
import LogTableReports from "./LogReportTable";
import ReportPagination from "components/ReportPagination";
import DownloadButton from "components/DownloadButton";

// Store
import { useStores } from "store";

// Utils
import {
    getWeekNumber,
    getFromDate,
    getToDate,
    checkIfWeekSelected,
} from "utils/managerReportsUtils";
import { getLogsParams } from "utils/managerReportsUtils";

function useStore() {
    const { logStore, exportStore, paginationStore } = useStores();

    return useObserver(() => ({
        logs: logStore.logs,
        projectList: logStore.projectList,
        userList: logStore.userList,
        onGetLogs: logStore.onGetLogs,
        onGetProjectList: logStore.onGetProjectList,
        onGetUserList: logStore.onGetUserList,
        onUpdateLogStatus: logStore.onUpdateLogStatus,
        onDownload: exportStore.onDownload,
        page: paginationStore.page,
        perPage: paginationStore.perPage,
        setInitialPagination: paginationStore.setInitialValues,
    }));
}

export default function ManagerReports() {
    const {
        logs,
        projectList,
        onGetLogs,
        userList,
        page,
        perPage,
        onUpdateLogStatus,
        onDownload,
        onGetUserList,
        onGetProjectList,
        setInitialPagination,
    } = useStore();

    const currentWeek = getWeekNumber(new Date());

    const [weekNumber, setWeekNumber] = useState(currentWeek);
    const [fromDate, setFromDate] = useState(getFromDate(currentWeek));
    const [toDate, setToDate] = useState(getToDate(currentWeek));
    const [selectedProjects, setSelectedProjects] = useState([]);
    const [selectedUsers, setSelectedUsers] = useState([]);

    useEffect(() => {
        onGetLogs({
            ...getLogsParams(fromDate, toDate, selectedUsers, selectedProjects),
            page: page,
            per_page: perPage,
        });
    }, [
        fromDate,
        onGetLogs,
        page,
        perPage,
        selectedProjects,
        selectedUsers,
        toDate,
    ]);

    useEffect(() => {
        if (weekNumber) {
            setFromDate(getFromDate(weekNumber));
            setToDate(getToDate(weekNumber));
        }
    }, [weekNumber]);

    useEffect(() => {
        if (checkIfWeekSelected(fromDate, toDate)) {
            setWeekNumber(getWeekNumber(fromDate));
            return;
        }
        setWeekNumber(null);
    }, [fromDate, toDate]);

    const onFilterChange = useCallback(
        (type, value) => {
            setInitialPagination();
            const setValueTypes = {
                fromDate: () => setFromDate(value),
                toDate: () => setToDate(value),
                weekNumber: () => setWeekNumber(value),
                selectedProjects: () => setSelectedProjects(value),
                selectedUsers: () => setSelectedUsers(value),
            };

            setValueTypes[type]();
        },
        [setInitialPagination]
    );

    useEffect(() => {
        onGetProjectList();
        onGetUserList();
    }, [onGetProjectList, onGetUserList]);

    const onDownloadFile = () => {
        onDownload({
            from_date: fromDate,
            to_date: toDate,
            week: weekNumber,
            users: selectedUsers.map(item => item.id),
            projects: selectedProjects.map(item => item.id),
        });
    };

    return (
        <Container>
            <FilterSelectorPanel
                fromDate={fromDate}
                toDate={toDate}
                weekNumber={weekNumber}
                selectedProjects={selectedProjects}
                selectedUsers={selectedUsers}
                setFilter={onFilterChange}
                projectList={projectList}
                logs={logs}
                userList={userList}
            />
            <Box mt={4}>
                <Paper>
                    <LogTableReports
                        logList={logs}
                        userList={userList}
                        projectList={projectList}
                        onUpdateLogStatus={onUpdateLogStatus}
                    />
                    <ReportPagination />
                </Paper>
                <Box display="flex" justifyContent="flex-end" my={3}>
                    {logs.length > 0 && (
                        <DownloadButton handleClick={onDownloadFile}>
                            Export
                        </DownloadButton>
                    )}
                </Box>
            </Box>
        </Container>
    );
}
